import React, { useState } from 'react';
import './InputForm.css'; // import your CSS file

const InputForm = () => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [error, setError] = useState(null);

  const handleInputChange = (event) => {
    setInputText(event.target.value);
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const url = 'https://api.openai.com/v1/completions';
    const data = {
      model: "text-davinci-003",
      prompt: inputText,
      temperature: 0,
      max_tokens: 1000,
      top_p: 1.0,
      frequency_penalty: 0.2,
      presence_penalty: 0.0
    };
    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: 'Bearer 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',
      },
      body: JSON.stringify(data),
    });
    if (!response.ok) {
      setError(response.statusText);
      return;
    }
    const result = await response.json();
    setOutputText(result.choices[0].text);
    setError(null);
  };

  return (
    <div className="input-form-container">
      <form onSubmit={handleSubmit}>
        <label className="input-label">
          Enter some text:
          <input type="text" value={inputText} onChange={handleInputChange} className="input-field" />
        </label>
        <button type="submit" className="submit-button">Submit</button>
      </form>
      {error && <p className="error">{error}</p>}
      {outputText && <p className="output">{outputText}</p>}
    </div>
  );
};

export default InputForm;
